import React from "react";

function Taskbar({ setcurrent_app }) {
  const showCalc = () => {
    const application = document.getElementById("calc");
    if (application.style.bottom === `40px`) {
      application.style.bottom = `-500px`;
    } else {
      application.style.bottom = `40px`;
      application.style.zIndex = `20`;
    }
  };

  let interval = 0;
  const date = () => {
    const current = new Date();
    document.getElementById("time").innerHTML =
      current.getHours() + ":" + current.getMinutes();
    document.getElementById("date").innerHTML =
      current.getDate() +
      "/" +
      current.getMonth() +
      "/" +
      current.getFullYear();
    interval = 50000;
  };
  setInterval(date, interval);

  return (
    <div className="taskbar" id="taskbar">
      <div className="taskbar-btns">
        <button className="taskbar-btn" id="start"></button>
        <button className="taskbar-btn" id="store"></button>
        <button
          className="taskbar-btn"
          id="explorer"
          onClick={() => {
            setcurrent_app("files-app");
          }}
        ></button>
        <button className="taskbar-btn" id="mails"></button>
        <button
          className="taskbar-btn"
          id="notepad"
          onClick={() => {
            setcurrent_app("notepad-app");
          }}
        ></button>
        <button
          className="taskbar-btn"
          id="chrome"
          onClick={() => {
            setcurrent_app("chrome-app");
          }}
        ></button>
        <button
          className="taskbar-btn"
          id="calculator"
          onClick={showCalc}
        ></button>
        <button
          className="taskbar-btn"
          id="vscode"
          onClick={() => {
            setcurrent_app("vscode-app");
          }}
        ></button>
      </div>
      <div className="taskbar-right">
        <div className="util">
          <div
            id="battery"
            onMouseLeave={() => {
              setTimeout(() => {
                const battery = document.getElementById("battery-details");
                battery.style.opacity = 0;
                setTimeout(() => {
                  battery.style.bottom = "-500px";
                }, 100);
              }, 900);
            }}
            onClick={() => {
              const battery = document.getElementById("battery-details");
              if (battery.style.bottom === "50px") {
                battery.style.opacity = 0;
                setTimeout(() => {
                  battery.style.bottom = "-500px";
                }, 100);
              } else {
                battery.style.opacity = 1;
                battery.style.bottom = "50px";
              }
            }}
          >
            <div className="battery-details" id="battery-details">
              <font style={{ fontSize: "19px" }}>Battery</font>
              <p>Charging Now</p>
              <p>75% chaged now</p>
              <p>20min to be full</p>
            </div>
          </div>
          <div
            id="wifi"
            onMouseLeave={() => {
              setTimeout(() => {
                const wifi = document.getElementById("wifi-details");
                wifi.style.opacity = 0;
                setTimeout(() => {
                  wifi.style.bottom = "-500px";
                }, 200);
              }, 900);
            }}
            onClick={() => {
              const wifi = document.getElementById("wifi-details");
              if (wifi.style.bottom === "50px") {
                wifi.style.opacity = 0;
                setTimeout(() => {
                  wifi.style.bottom = "-500px";
                }, 200);
              } else {
                wifi.style.opacity = 1;
                wifi.style.bottom = "50px";
              }
            }}
          >
            <div className="wifi-details" id="wifi-details">
              <font style={{ fontSize: "19px" }}>Available networks</font>
              <div
                style={{
                  margin: "10px 5px 0px 5px",
                  textAlign: "center",
                  height: "60px",
                  width: "96%",
                  padding: "5px",
                  borderTop: " 1px solid grey",
                  borderBottom: " 1px solid grey",
                }}
              >
                <font style={{ fontSize: "16px", fontWeight: "500" }}>
                  Kolorav's WLAN
                </font>
                <p id="connected" style={{ fontSize: "12px" }}>
                  connected
                </p>
              </div>
            </div>
          </div>
        </div>
        <div
          className="date-time"
          style={{
            display: "flex",
            flexDirection: "column",
            width: "85px",
            height: "100%",
            textAlign: "center",
            justifyContent: "center",
            fontFamily: "ubuntu mono",
          }}
        >
          <font id="time" style={{ fontFamily: "ubuntu mono" }}></font>
          <font id="date" style={{ fontFamily: "ubuntu mono" }}></font>
        </div>
        <div
          id="sidebar-icon"
          onClick={() => {
            const sidebar = document.getElementById("sidebar");
            if (sidebar.style.right === `0px`) {
              sidebar.style.right = "-600px";
            } else {
              sidebar.style.right = "0px";
            }
          }}
        ></div>
      </div>
    </div>
  );
}

export default Taskbar;
