import React from "react";

function Intro() {
  return (
    <div
      id="welcome"
      style={{
        fontSize: "25px",
        display: "flex",
        flexDirection: "column",
        background: "transparent",
        width: "70%",
        height: "90%",
        marginTop: "130px",
        alignItems: "center",
        justifyContent: "center",
        padding: "25px",
        position: "absolute",
        top: "120%",
        transitionDuration: "0.5s",
      }}
    >
      <font style={{ fontSize: "65px", color: "white", fontWeight: "550" }}>
        Welcome to Windows 11
      </font>
      <font style={{ fontSize: "65px", color: "white" }}>The newest one</font>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          padding: "40px",
          fontSize: "33px",
          background: "trasparent",
          opacity: 0.8,
        }}
      >
        <font
          style={{
            color: "white",
            fontWeight: "300",
            textAlign: "center",
            fontFamily: "Satisfy",
          }}
        >
          website might crash on small window
        </font>
        <font
          style={{
            color: "white",
            fontWeight: "300",
            textAlign: "center",
            fontFamily: "Satisfy",
          }}
        >
          at least 1100 x 640 recomended
        </font>
      </div>
    </div>
  );
}

export default Intro;
