import { lazy, Suspense, useEffect, useState } from "react";
import "./App.css";
import Intro from "./components/Intro";
import Taskbar from "./components/Taskbar";
const Calculator = lazy(() => import("./components/Calculator"));
const Chrome = lazy(() => import("./components/Chrome"));
const Explorer = lazy(() => import("./components/Explorer.js"));
const Notepad = lazy(() => import("./components/Notepad"));
const Sidebar = lazy(() => import("./components/Sidebar"));

const Vscode = lazy(() => import("./components/Vscode"));

function App() {
  const [current_app, setcurrent_app] = useState("");
  const [prev_app, setprev_app] = useState("");

  const show = () => {
    const application = document.getElementById(current_app);
    if (application.style.top !== "0%") {
      application.style.top = "0%";
    } else {
      application.style.top = "120%";
    }
    setprev_app(current_app);
  };
  useEffect(() => {
    const btns = document.getElementsByClassName("taskbar-btn");
    for (const btn of btns) {
      btn.disabled = true;
    }
    setTimeout(() => {
      document.getElementById("welcome").style.top = "-10%";
      setTimeout(() => {
        document.getElementById("welcome").style.top = "120%";
        for (const btn of btns) {
          btn.disabled = false;
        }
      }, 3200);
    }, 1000);
  }, []);

  useEffect(
    () => {
      if (current_app !== "") {
        if (prev_app !== "") {
          const application = document.getElementById(prev_app);
          application.style.top = "120%";
        }
        show();
      }
    }, // eslint-disable-next-line
    [current_app]
  );

  useEffect(() => {
    const closes = document.getElementsByClassName("close");
    for (const element of closes) {
      element.addEventListener("click", () => {
        setcurrent_app("");
      });
    }
  });

  const width = window.innerWidth;
  const height = window.innerHeight;
  if (width < 1100 || height < 660) {
    return (
      <div
        style={{
          width: "100%",
          height: "100%",
          display: "flex",
          textAlign: "center",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
          padding: "100px",
        }}
      >
        <h1>
          For experiencing this website you must have screen 1100px x 640px or
          more
        </h1>
        <h1>Sorry!</h1>
      </div>
    );
  }
  return (
    <div className="App">
      <div className="holder">
        <Intro />
        <Suspense fallback={<div>...</div>}>
          <Sidebar />
        </Suspense>
        <div className="calc" id="calc">
          <Suspense fallback={<div>...</div>}>
            <Calculator />
          </Suspense>
        </div>
        <div id="files-app" className="running_app">
          <div className="app-holder-center">
            <Suspense fallback={<div>...</div>}>
              <Explorer app={"files-app"} />
            </Suspense>
          </div>
        </div>
        <div id="vscode-app" className="running_app">
          <div className="app-holder-center">
            <Suspense fallback={<div>...</div>}>
              <Vscode app={"vscode-app"} />
            </Suspense>
          </div>
        </div>

        <div id="chrome-app" className="running_app">
          <div className="app-holder-center">
            <Suspense fallback={<div>...</div>}>
              <Chrome app={"chrome-app"} />
            </Suspense>
          </div>
        </div>
        <div id="notepad-app" className="running_app">
          <div className="app-holder-center">
            <Suspense fallback={<div>...</div>}>
              <Notepad app={"notepad-app"} />
            </Suspense>
          </div>
        </div>

        <Taskbar setcurrent_app={setcurrent_app} />
      </div>
    </div>
  );
}
export default App;
